.titlePage {
    font-size: 24px;
    margin-top: 20px;
    color: indigo;
    text-align: center;
}

.blockContacts {
    display: block;
    gap: 10px;
    padding: 25px 55px 25px 55px;
    /* max-width: 500px; */
    width: 500px;
    min-height: 300px;
    justify-content: center;
    border-radius: 20px;
    box-shadow: 0 0 30px rgba(69, 8, 125, 0.5);
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 25px;
    text-align: center;
}

.listContacts {
    text-align: center;
}

.textItem {
    font-size: 16px;
    color: indigo;
    margin-bottom: 5px;
    /* text-align: right; */
    list-style: none;
}

.textItem:hover,
.textItem:focus {
    background-color: lavender;
}